import { Injectable, Optional } from '@angular/core';

export class TabConfig {
  // 
  showDropDown = true;
  // 
  showTooltips = true;
  // 
  scrollStep = 15;
  //  ---- todo:windowresize
  autoResize = true;
  //  
  maxDropdownH = 200;
  //  
  maxDropdownW = -1;
  // 
  maxTabW = 100;
  // 
  maxTooltipLen = 5;
}

@Injectable()
export class TabService {
  config: TabConfig;
  constructor(@Optional() exConfig: TabConfig) {
    this.config = new TabConfig();
    if (exConfig) {
      Object.assign(this.config, exConfig);
    }
  }
  getConfig() {
    return this.config;
  }
}
