import { Directive, TemplateRef, ElementRef } from '@angular/core';
import { FarrisTabComponent } from './tab.component';
@Directive({
    selector: '[tabHead]'
})
export class TabHeadDirective {
    constructor(tabItem: FarrisTabComponent, el: ElementRef, template: TemplateRef<any>) {
        tabItem.headingRef = template;
    }
}
