import {
    Component, Input, TemplateRef, ChangeDetectorRef, Compiler,
    ComponentFactoryResolver, Renderer2, ElementRef,
    ContentChild, OnInit, AfterViewInit, ViewContainerRef, Optional, Type,
    NgModuleFactory,
    AfterViewChecked
} from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { FarrisTabsComponent } from '../tabs/tabs.component';
import { FarrisTabsExtendDirective } from './extend.directive';

@Component({
    selector: 'farris-tab',
    templateUrl: './tab.component.html'
})
export class FarrisTabComponent implements OnInit, AfterViewInit, AfterViewChecked {
    // tabsflex  flex
    // tslint:disable-next-line:no-inferrable-types
    // 
    setIntervalTab: any;
    
    @Input('width') tabWidth: number = -1;
    @Input() set show(value) {
        this.showStates.next(value);
        this._show = value;
    }
    get show(){
        return this._show;
    }
    @Input() asyncComponent: Type<any>;
    @Input() asyncModule: NgModuleFactory<any>;
    @Input() id: string;
    // 
    @Input() titleCustomCls = '';
    // 
    titleOverflow:false;
    // tab 
    @Input() title: string;
    // selected 
    // tslint:disable-next-line:no-inferrable-types
    selected: boolean = false;
    // disabled 
    @Input() disabled: boolean;
    // 
    @Input() removeable: boolean;
    @Input() toolbar: any;
    // @Input() content: string | Type<any>;
    // 
    @ContentChild('content') contentTempl: TemplateRef<any>;
    @ContentChild(FarrisTabsExtendDirective) farrisTabsExtendDirective: any;
    // appDiv: T;
    showStates = new BehaviorSubject(true);

    // 
    activeState = new BehaviorSubject(false);
    private _activeForOld: any;
    set _active(value: any) {
        this._activeForOld = value;
        this.activeState.next(value);
    }
    get _active() {
        return this._activeForOld;
    }
    view: ViewContainerRef;
    tempDirective: any;
    popupFlag: boolean;
    _show: boolean = true;
    // 
    get active() {
        return this._active;
    }
    headingRef: TemplateRef<any>;
    compOrHtml: any;
    constructor(private cp: Compiler, public componentFactoryResolver: ComponentFactoryResolver, private render: Renderer2,
        private el: ElementRef, private cef: ChangeDetectorRef, @Optional() public scrollTabs: FarrisTabsComponent) {
    }
    /**
     * 
     */
    ngOnInit() {
        this.scrollTabs.tabs.push(this);
        if (!this.scrollTabs.activeId) {
            this.scrollTabs.tabs[0]._active = true;
        } else {
            this.scrollTabs.load();
        }
    }
    ngAfterViewInit(): void {
        // HostBinding, class
        const nEl = this.el.nativeElement;
        this.activeState.subscribe(data => {
            this.render.addClass(nEl, 'farris-tabs-body');
            if (data) {
                this.render.addClass(nEl, 'f-tab-active');
                this.render.removeClass(nEl, 'f-tab-d-none');
            } else {
                this.render.addClass(nEl, 'f-tab-d-none');
                this.render.removeClass(nEl, 'f-tab-active');
            }
        });
    }
    ngAfterViewChecked() {

    }
    loadComponent() {

    }

}
